/**
 * mt-command-bar
 * @module components/CommandBar
 * @desc 操作栏
 * @param {String} label - 字段中文名
 * @param {String} [value] - 绑定值，支持双向绑定
 * @param {Array} options - 选项数据
 * @example
 */
import Actionsheet from 'bh-mint-ui2/packages/actionsheet/index.js';
export default {
  name: 'mt-command-bar',
  props: {
    buttons: { type: Array, default: [] },
    moreButtons: { type: Array, default: [] }
  },
  data: function(){
    return {
      sheetVisible:false
    }
  },
  methods: {
    showMoreAction: function() {
      this.sheetVisible = true;
    }
  },
  components: { Actionsheet }
};



